import React from 'react';
import { Zoom } from 'react-slideshow-image';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

const query = graphql`
    query {
        allFile(filter: { sourceInstanceName: { eq: "slide" } }) {
            edges {
                node {
                    childImageSharp {
                        fluid(maxWidth: 3840, quality: 100) {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
        }
    }
`;

const zoomIn = {
    duration: 5000,
    transitionDuration: 500,
    indicators: true,
    scale: 1.2,
    infinite: true,
    arrows: false,
};

export default () => {
    const data = useStaticQuery(query);
    const images = data.allFile.edges.map(slide => {
        return slide.node.childImageSharp.fluid;
    });

    return (
        <Zoom {...zoomIn}>
            {images.map((slide, index) => (
                <Img
                    key={index}
                    fluid={slide}
                    style={{ width: `100%`, height: `70vh` }}
                />
            ))}
        </Zoom>
    );
};
